.tag-logo {
  width: 40px;
  height: 40px;
}

.container {
  margin: 0;
  padding-top: $grid-gutter-width / 2;
  padding-bottom: $grid-gutter-width / 2;
  background: #fff;
  border-radius: $border-radius;
}

.form-control {
  padding: .25rem .5rem;
  font-size: .875rem;
  line-height: 1.5;
  border-radius: .2rem;
}

textarea {
  font-family: monospace;
}

.form-control-hidden {
  display: none !important;
}

.form-inline {

  & > *:not(:last-child) {
    margin-right: 0.5rem !important;
  }

  input {
    max-width: 180px !important;
  }

  input[type="number"] {
    max-width: 70px !important;
  }

}

.control-label {
  text-align: right;
  margin-bottom: 0;
  padding-top: 7px;
}

.label-inline {
  line-height: 26px;
}

.badge {
  font-size: 80%;
}

/**
 *POPOVER
 */
.popover {

  .arrow {
    overflow: initial;
  }

  .arrow:after {
    left: initial;
    top: initial;
    width: auto;
    height: auto;
    transform: none;
    //z-index: auto;
    background-color: initial;
    box-shadow: none;
  }

  &.bs-popover-top {
    .arrow {
      margin-left: .5rem;
    }
  }

  &.bs-popover-right {
    .arrow {
      margin-left: 0;
    }
  }

  .popover-body {
    ul {
      padding-left: 1.5rem;
      list-style: initial;
      list-style-position: outside;
    }
  }

}

.btn-link {
  cursor: pointer;
}

.btn-save {

  color: color-yiq(#EF7908);
  @include gradient-bg(#EF7908);
  border-color: #EF7908;
  @include box-shadow($btn-box-shadow);

  @include hover {
    color: color-yiq(darken(#EF7908, 7.5%));
    @include gradient-bg(darken(#EF7908, 7.5%));
    border-color: darken(#EF7908, 10%);
  }

  &:focus,
  &.focus {
    // Avoid using mixin so we can pass custom focus shadow properly
    @if $enable-shadows {
      box-shadow: $btn-box-shadow, 0 0 0 $input-btn-focus-width rgba(#EF7908, .5);
    } @else {
      box-shadow: 0 0 0 $input-btn-focus-width rgba(#EF7908, .5);
    }
  }

  // Disabled comes first so active can properly restyle
  &.disabled,
  &:disabled {
    background-color: #EF7908;
    border-color: #EF7908;
  }

  &:not([disabled]):not(.disabled):active,
  &:not([disabled]):not(.disabled).active,
  .show > &.dropdown-toggle {
    color: color-yiq(darken(#EF7908, 10%));
    background-color: darken(#EF7908, 10%);
    @if $enable-gradients {
      background-image: none; // Remove the gradient for the pressed/active state
    }
    border-color: darken(#EF7908, 12.5%);

    // Avoid using mixin so we can pass custom focus shadow properly
    @if $enable-shadows {
      box-shadow: $btn-active-box-shadow, 0 0 0 $input-btn-focus-width rgba(#EF7908, .5);
    } @else {
      box-shadow: 0 0 0 $input-btn-focus-width rgba(#EF7908, .5);
    }
  }

}

.indicator {
  display: inline-block;
  margin: 4px 0;
  margin-right: .25rem;
  padding: 0 .75rem;
  height: 25px;
  min-width: 46px;
  border: 1px solid rgba(0, 0, 0, .125);
  border-radius: .25rem;
  line-height: 16px;
  font-size: 10px;
  text-align: center;
  vertical-align: middle;
  user-select: none;

  &::after {
    content: " ";
    display: block;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 4px;
    background: $teal;
    border-radius: .25em;
  }

  &.indicator-off {
    &::after {
      background: $red;
    }
  }
}

.indicator-label {
  display: inline-block;
  margin-bottom: .25rem;
  font-size: 13px;
}

.collapse-inner {
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.col-offset-left {
  padding-left: 35px !important;
}

.panel {
  margin-bottom: 1rem;
  padding: 1.25rem;
  word-wrap: break-word;
  background-color: #fff;
  background-clip: border-box;
  border: 1px solid rgba(0, 0, 0, .125);
  border-radius: .25rem;
}

.panel-title {
  margin-bottom: 1rem;
}

.section-title {
  padding: .75rem 1rem;
  margin-bottom: 1rem;
  background-color: $gray-300;
  border-radius: .25rem;
}

.label {
  font-size: 13px;
}

.custom-control-indicator {
  top: 0;
}

.custom-control-description {
  font-weight: 500;
}

.custom-control.custom-radio + .form-inline {
  margin-bottom: .5rem;
  padding-left: 1.5rem;

  input {
    max-width: 140px !important;
  }
}

.custom-controls-stacked .custom-control:last-child {
  margin-bottom: 0;
}

#table-custom-events {
  .custom-control {
    margin-right: 0;
    margin-bottom: 0;
    top: 2px;
    min-height: 17px;
  }

  tr.disabled {
    background-color: #f8f9fa;
  }

  td.networks .fa {
    margin-right: 5px;
  }
}

/**
 * CARDS
 */

.card {
  margin-bottom: 1rem;

  // overrides default WordPress styles
  margin-top: 0;
  padding: 0;
  max-width: 100%;
  box-shadow: none;
  background: none;
}

.card:not(.card-static) {
  .card-body {
    display: none;
  }
}

.card-collapse {
  float: right !important;
  cursor: pointer;
}

.card-header {
  border-bottom: none;
}

.card-body {
  border-top: $card-border-width solid $card-border-color;
}

.panel-primary,
.card-primary {
  color: #fff;
  background-color: $color-brand-blue;
  border-color: $color-brand-blue;
}

/**
 * CUSTOM EVENTS
 */

#table-custom-events {

  .event-actions {
    display: block;
    margin-top: .25rem;
    font-size: 10px;
    visibility: hidden;

    a {
      text-decoration: none;
    }
  }

  tr:hover {
    .event-actions {
      visibility: visible;
    }
  }

}

/**
 * FACEBOOK PARAMS VISIBILITY
 */

#facebook_params_panel {

  .ViewContent,
  .Search,
  .AddToCart,
  .AddToWishlist,
  .InitiateCheckout,
  .AddPaymentInfo,
  .Purchase,
  .Lead,
  .CompleteRegistration,
  .Subscribe,
  .StartTrial {
    display: none;
  }

  &.ViewContent {
    .ViewContent {
      display: flex;
    }
  }

  &.Search {
    .Search {
      display: flex;
    }
  }

  &.AddToCart {
    .AddToCart {
      display: flex;
    }
  }

  &.AddToWishlist {
    .AddToWishlist {
      display: flex;
    }
  }

  &.InitiateCheckout {
    .InitiateCheckout {
      display: flex;
    }
  }

  &.AddPaymentInfo {
    .AddPaymentInfo {
      display: flex;
    }
  }

  &.Purchase {
    .Purchase {
      display: flex;
    }
  }

  &.Lead {
    .Lead {
      display: flex;
    }
  }

  &.CompleteRegistration {
    .CompleteRegistration {
      display: flex;
    }
  }

  &.Subscribe {
    .Subscribe {
      display: flex;
    }
  }

  &.StartTrial {
    .StartTrial {
      display: flex;
    }
  }

}

/**
 * SYSTEM REPORT
 */

.table.system-report {
  margin-bottom: 0;

  tr:first-of-type {
    td {
      border-top: none;
    }
  }
}

/**
 * WP ADMIN
 */

.notice {
  p {
    margin: 0.5em 0 !important;
  }
}