// Custom switcher component for Bootstrap 4
// Based on https://github.com/haubek/bootstrap4c-custom-switch

$switch-unit:                     px;
$switch-width:                    66;
$switch-height:                   25;
$switch-font-size:                12;
$switch-font-family:              $font-family-sans-serif;

$switch-checked-bg:               $blue;
$switch-unchecked-bg: $color-disabled-bg;

.custom-switch {
  display: inline;
  margin-right: .25rem;

  .custom-switch-input {
    display: none;
    &,
    &:after,
    &:before,
    & *,
    & *:after,
    & *:before,
    & + .custom-switch-btn {
      box-sizing: border-box;
      &::selection {
        background: none;
      }
    }
    + .custom-switch-btn {
      outline: 0;
      display: inline-block;
      position: relative;
      user-select: none;
      cursor: pointer;
      width: ($switch-width + $switch-unit);
      height: ($switch-height + $switch-unit);
      margin: 4px 0;
      padding: 4px;
      background: $switch-unchecked-bg;
      background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='66' height='#{$switch-height}'><text x='#{($switch-width / 1.75)}' y='17' font-size='12px' font-weight='500' font-family='#{$switch-font-family}' fill='#{$color-disabled}'>Off</text></svg>");
      border-radius: .25rem;
      transition: all 300ms ease;
      &:after,
      &:before {
        position: relative;
        display: block;
        content: "";
        width: 25px;
        height: (($switch-height - 8) + $switch-unit);
      }
      &:after {
        left: 0;
        border-radius: .25rem;
        background: #fff;
        transition: all 300ms ease;
      }
      &:before {
        display: none;
      }
    }
    &:checked + .custom-switch-btn {
      background: $switch-checked-bg;
      background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='66' height='#{$switch-height}'><text x='#{($switch-width / 7)}' y='17' font-size='12px' font-weight='500' font-family='#{$switch-font-family}' fill='#fff'>On</text></svg>");
      &:after {
        left: 33px;
      }
      ~ .custom-switch-content-checked {
        opacity: 1;
        height: auto;
      }
      ~ .custom-switch-content-unchecked {
        display: none;
        opacity: 0;
        height: 0;
      }
    }
    &:not(:checked) + .custom-switch-btn {
      ~ .custom-switch-content-checked {
        display: none;
        opacity: 0;
        height: 0;
      }
      ~ .custom-switch-content-unchecked {
        opacity: 1;
        height: auto;
      }
    }

    &:disabled + .custom-switch-btn {
      cursor: default;
    }

  }

  &.disabled {
    //opacity: 0.6;
  }
}

.switcher-label {
  display: inline;
  margin-bottom: .25rem;
  font-size: 13px;
}